<template>
    <div>
        <h2>메인페이지</h2>
        <input v-model="title"><br>
        <input v-model="content"><br>
        <button @click="sub">전송</button>
        <hr>
        <table>
            <tr v-for="(article, idx) in articleList" :key="idx">
                <td>{{ article.num }}</td>
                <td><router-link :to="`/article/${article.num}`">{{ article.title }}</router-link></td>
            </tr>
        </table>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            title: "",
            content: "",
            articleList: []
        }
    },
    created() {
        this.loadArticle();
    },
    methods: {
        loadArticle() {
            axios.get("http://localhost:9999/board/getList").then(({data}) => {
                this.articleList = [];
                for (var d of data) {
                    this.articleList.push(d);
                }
            })
        },
        async sub() {
            await axios.post("http://localhost:9999/board/write", {
                title: this.title,
                content: this.content
            })
            this.loadArticle();
        }
    },
};
</script>

